.query{
  .title {
    display: flex; align-items: center; padding:16px 10px 14px; border-bottom: #ddd 1px solid;
      li{ font-size:18px; margin-right: 10px;
       a svg{width:34px; height:34px; padding-top:6px;}
      }
  }
  .querybar{
    background-color: #ddd; width:90%; margin:20px auto; border-radius: 6px; position: relative;
       input{ border:none; width:100%; padding: 10px 34px; background-color: transparent; background: url('../../assets/images/icon_query.png') no-repeat 2% 56%/contain; background-size: 5%; outline: none;}
       button{position:absolute; right:10px; top:50%; transform: translateY(-50%); padding:4px;}
  }

 .result{
  height:calc(100vh - 220px);

  ul {overflow-y: scroll;
    li a{
      width:90%; margin:12px auto; display: grid; grid-template-columns: 2fr 8fr; border-radius: 10px; font-size: 12px; color:#777; overflow: hidden; border:1px #eee solid; 

      .imgWrap{ width:90%; height: 90%; padding: 6px 0 0 6px;
         img{width:100%; height: 100%;}
      }
     
      .txt{ padding: 14px; background-color: #eee;
        h4{font-size: 14px; font-weight: bold;}
        p{margin-top:6px}
      }
    }
  }
 }
}